.ailment-card .side .card-content {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 5px;
}
.ailment-card .side .card-content .mushroom-tile {
  width: 150px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  overflow: auto;
  padding: 5px;
}
.ailment-card .side .card-content .mushroom-tile .image-container {
  height: 150px;
  background-color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ailment-card .side .card-content .mushroom-tile .image-container img {
  height: auto;
  width: auto;
  max-height: 100%;
  max-width: 100%;
}
.ailment-card .side .card-content .mushroom-tile .mushroom-info {
  background-color: #ddd;
  flex: 1;
}/*# sourceMappingURL=ailment-card.css.map */