.images {
    width: fit-content;
    background-color: #000;
    flex-direction: column;
    float: left;
    margin: 8px;
    padding: 10px;

    &,
    & > div {
        display: flex;
        justify-content: center;
        gap: 10px;
    }

    img {
        width: auto;
    }

    .main-image {
        img {
            height: 200px;
        }
    }

    .other-images {
        img {
            height: 80px;
        }
    }
}
