.images {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-color: #000;
  flex-direction: column;
  float: left;
  margin: 8px;
  padding: 10px;
}
.images, .images > div {
  display: flex;
  justify-content: center;
  gap: 10px;
}
.images img {
  width: auto;
}
.images .main-image img {
  height: 200px;
}
.images .other-images img {
  height: 80px;
