.ailment-card {
    .side {
        .card-content {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            padding: 5px;

            .mushroom-tile {
                // height: 200px;
                width: 150px;
                box-sizing: border-box;
                display: flex;
                flex-direction: column;
                overflow: auto;
                padding: 5px;

                .image-container {
                    height: 150px;
                    background-color: #000;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    img {
                        height: auto;
                        width: auto;
                        max-height: 100%;
                        max-width: 100%;
                    }
                }

                .mushroom-info {
                    background-color: #ddd;
                    flex: 1;
                }
            }
        }
    }
}
